import WheelGesturesPlugin from "embla-carousel-wheel-gestures";
import { Carousel, CarouselContent } from "../ui/carousel";

export function SmartCarousel({
  wheel = false,
  className,
  children,
}: {
  wheel?: boolean;
  className?: string;
  children: React.ReactNode;
}) {
  return (
    <Carousel
      className={className}
      opts={{ align: "start" }}
      plugins={
        wheel
          ? [
              WheelGesturesPlugin({
                forceWheelAxis: "x",
              }),
            ]
          : undefined
      }
    >
      <CarouselContent className="-ml-3">{children}</CarouselContent>
    </Carousel>
  );
}
