"use client";

import * as React from "react";
import { z } from "zod";
import { useForm } from "react-hook-form";
import { zodResolver } from "@hookform/resolvers/zod";
import { useState, useEffect } from "react";

import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";

import {
  Dialog,
  DialogContent,
  DialogFooter,
  DialogHeader,
  DialogTitle,
  DialogDescription,
} from "@/components/ui/dialog";

import {
  Form,
  FormField,
  FormItem,
  FormLabel,
  FormControl,
  FormMessage,
} from "@/components/ui/form";

import { CarouselItem } from "@/components/ui/carousel";
import { addSchema, CATEGORIES } from "@/lib/validations/service";
import { SmartCarousel } from "@/components/common/smart-carousel";
import { CategoryPill } from "@/components/services/cartegory-pill";

type ServiceRow = {
  id: string;
  title: string;
  cat: string;
  dur: string;
  price: string;
  image?: string;
};

const INITIAL_FEATURED: ServiceRow[] = [
  {
    id: "svc-1",
    title: "Men's Classic Haircut",
    cat: "Haircuts",
    dur: "45 min",
    price: "$50.00",
    image:
      "https://images.unsplash.com/photo-1517836357463-d25dfeac3438?q=80&w=1200&auto=format&fit=crop",
  },
  {
    id: "svc-2",
    title: "Full Head Highlights",
    cat: "Coloring",
    dur: "120 min",
    price: "$250.00",
    image:
      "https://images.unsplash.com/photo-1519741497674-611481863552?q=80&w=1200&auto=format&fit=crop",
  },
  {
    id: "svc-3",
    title: "Blowout & Style",
    cat: "Styling",
    dur: "60 min",
    price: "$75.00",
    image:
      "https://images.unsplash.com/photo-1511367461989-f85a21fda167?q=80&w=1200&auto=format&fit=crop",
  },
];

const INITIAL_TABLE: ServiceRow[] = [
  {
    id: "t-1",
    title: "Women's Haircut",
    cat: "Haircuts",
    dur: "60 min",
    price: "$85.00",
    image:
      "https://images.unsplash.com/photo-1519741497674-611481863552?q=80&w=1200&auto=format&fit=crop",
  },
  {
    id: "t-2",
    title: "Keratin Treatment",
    cat: "Treatments",
    dur: "180 min",
    price: "$300.00",
    image:
      "https://images.unsplash.com/photo-1519681393784-d120267933ba?q=80&w=1200&auto=format&fit=crop",
  },
];

type AddValues = z.infer<typeof addSchema>;
type EditValues = AddValues;

function FeaturedCard({
  item,
  onEdit,
  onDelete,
}: {
  item: ServiceRow;
  onEdit: () => void;
  onDelete: () => void;
}) {
  return (
    <div className="w-[260px] sm:w-[300px] lg:w-[340px] shrink-0 rounded-xl border border-border-light dark:border-border-dark bg-content-light dark:bg-content-dark overflow-hidden">
      <div
        className="aspect-video bg-cover bg-center"
        style={{ backgroundImage: `url(${item.image})` }}
      />
      <div className="p-4 flex flex-col gap-2">
        <div className="flex items-start justify-between gap-3">
          <div className="min-w-0">
            <p className="font-semibold truncate">{item.title}</p>
            <p className="text-sm opacity-70">
              {item.cat} • {item.dur}
            </p>
          </div>
          <span className="text-sm font-medium">{item.price}</span>
        </div>
        <div className="flex gap-2">
          <Button variant="secondary" className="h-8 px-3" onClick={onEdit}>
            Edit
          </Button>
          <Button variant="destructive" className="h-8 px-3" onClick={onDelete}>
            Delete
          </Button>
        </div>
      </div>
    </div>
  );
}

export default function ManageServicesPage() {
  const [category, setCategory] = useState<(typeof CATEGORIES)[number]>("All");
  const [search, setSearch] = React.useState("");

  const [featured, setFeatured] = useState<ServiceRow[]>(INITIAL_FEATURED);
  const [rows, setRows] = useState<ServiceRow[]>(INITIAL_TABLE);

  const [open, setOpen] = useState(false);
  const [editing, setEditing] = useState<ServiceRow | null>(null);

  /* ---------- Add Form ---------- */
  const addForm = useForm<AddValues>({
    resolver: zodResolver(addSchema),
    defaultValues: {
      title: "",
      category: "Haircuts",
      durationMin: 45,
      price: 50,
      image: "",
    },
  });

  const previewAddImage = addForm.watch("image");

  function onAdd(values: AddValues) {
    const id = crypto.randomUUID?.() ?? Math.random().toString(36);

    const newRow: ServiceRow = {
      id,
      title: values.title.trim(),
      cat: values.category,
      dur: `${values.durationMin} min`,
      price: `$${values.price.toFixed(2)}`,
      image: values.image || undefined,
    };

    setRows((prev) => [newRow, ...prev]);
    if (values.category === "Haircuts") {
      setFeatured((prev) => [newRow, ...prev]);
    }
    addForm.reset();
  }

  /* ---------- Edit Form ---------- */
  const editForm = useForm<EditValues>({
    resolver: zodResolver(addSchema),
  });

  useEffect(() => {
    if (!editing) return;

    editForm.reset({
      title: editing.title,
      category: editing.cat as string,
      durationMin: parseInt(editing.dur),
      price: Number(editing.price.replace(/[^0-9.]/g, "")),
      image: editing.image ?? "",
    });
  }, [editing]);

  function openEditModal(s: ServiceRow) {
    setEditing(s);
    setOpen(true);
  }

  function deleteService(s: ServiceRow) {
    setFeatured((p) => p.filter((x) => x.id !== s.id));
    setRows((p) => p.filter((x) => x.id !== s.id));
  }

  function onEditSubmit(v: EditValues) {
    if (!editing) return;

    const updated: ServiceRow = {
      ...editing,
      title: v.title.trim(),
      cat: v.category,
      dur: `${v.durationMin} min`,
      price: `$${v.price.toFixed(2)}`,
      image: v.image || undefined,
    };

    setRows((p) => p.map((x) => (x.id === updated.id ? updated : x)));
    setFeatured((p) => p.map((x) => (x.id === updated.id ? updated : x)));

    setOpen(false);
    setEditing(null);
  }

  /* ---------- Filtering ---------- */
  const filteredRows = rows.filter((r) => {
    const matchCat = category === "All" || r.cat === category;
    const q = search.toLowerCase();

    const matchQ =
      !q ||
      r.title.toLowerCase().includes(q) ||
      r.cat.toLowerCase().includes(q) ||
      r.dur.toLowerCase().includes(q) ||
      r.price.toLowerCase().includes(q);

    return matchCat && matchQ;
  });

  return (
    <div className="bg-background-light dark:bg-background-dark min-h-screen">
      <main className="p-6 lg:p-10 max-w-7xl mx-auto space-y-10">
        {/* Page Header */}
        <header>
          <h1 className="text-3xl font-black">Manage Services</h1>
          <p className="opacity-70">
            Add, edit, and manage the services your salon offers.
          </p>
        </header>

        {/* Category Carousel (NO wheel gestures) */}
        <SmartCarousel className="mb-8">
          {CATEGORIES.map((c) => (
            <CarouselItem key={c} className="basis-auto pl-3">
              <CategoryPill
                label={c}
                active={c === category}
                onClick={() => setCategory(c)}
              />
            </CarouselItem>
          ))}
        </SmartCarousel>

        <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
          {/* ================= ADD SERVICE ================= */}
          <section className="lg:col-span-1">
            <div className="p-6 rounded-xl border bg-content-light dark:bg-content-dark">
              <h2 className="text-xl font-bold mb-6">Add a New Service</h2>

              <Form {...addForm}>
                <form
                  className="space-y-5"
                  onSubmit={addForm.handleSubmit(onAdd)}
                >
                  {/* Title */}
                  <FormField
                    control={addForm.control}
                    name="title"
                    render={({ field }) => (
                      <FormItem>
                        <FormLabel>Service Name</FormLabel>
                        <FormControl>
                          <Input className="h-12" {...field} />
                        </FormControl>
                        <FormMessage />
                      </FormItem>
                    )}
                  />

                  {/* Duration + Price */}
                  <div className="grid grid-cols-2 gap-4">
                    <FormField
                      control={addForm.control}
                      name="durationMin"
                      render={({ field }) => (
                        <FormItem>
                          <FormLabel>Duration (min)</FormLabel>
                          <FormControl>
                            <Input type="number" className="h-12" {...field} />
                          </FormControl>
                          <FormMessage />
                        </FormItem>
                      )}
                    />

                    <FormField
                      control={addForm.control}
                      name="price"
                      render={({ field }) => (
                        <FormItem>
                          <FormLabel>Price ($)</FormLabel>
                          <FormControl>
                            <Input type="number" className="h-12" {...field} />
                          </FormControl>
                          <FormMessage />
                        </FormItem>
                      )}
                    />
                  </div>

                  {/* Category */}
                  <FormField
                    control={addForm.control}
                    name="category"
                    render={({ field }) => (
                      <FormItem>
                        <FormLabel>Category</FormLabel>
                        <Select
                          value={field.value}
                          onValueChange={field.onChange}
                        >
                          <FormControl>
                            <SelectTrigger className="h-12">
                              <SelectValue />
                            </SelectTrigger>
                          </FormControl>
                          <SelectContent>
                            {CATEGORIES.filter((c) => c !== "All").map((c) => (
                              <SelectItem key={c} value={c}>
                                {c}
                              </SelectItem>
                            ))}
                          </SelectContent>
                        </Select>
                        <FormMessage />
                      </FormItem>
                    )}
                  />

                  {/* Image URL */}
                  <FormField
                    control={addForm.control}
                    name="image"
                    render={({ field }) => (
                      <FormItem>
                        <FormLabel>Image URL (optional)</FormLabel>
                        <FormControl>
                          <Input className="h-12" {...field} />
                        </FormControl>
                        <FormMessage />
                      </FormItem>
                    )}
                  />

                  {/* Preview */}
                  {previewAddImage && (
                    <div className="rounded overflow-hidden border">
                      <div
                        className="aspect-video bg-cover bg-center"
                        style={{ backgroundImage: `url(${previewAddImage})` }}
                      />
                    </div>
                  )}

                  {/* Buttons */}
                  <div className="flex gap-3">
                    <Button
                      type="submit"
                      className="flex-1 h-10 bg-primary text-white"
                    >
                      Add Service
                    </Button>
                    <Button
                      type="button"
                      variant="secondary"
                      className="flex-1 h-10"
                      onClick={() => addForm.reset()}
                    >
                      Clear
                    </Button>
                  </div>
                </form>
              </Form>
            </div>
          </section>

          {/* ================= RIGHT COLUMN ================= */}
          <section className="lg:col-span-2">
            <div className="p-6 rounded-xl border bg-content-light dark:bg-content-dark">
              {/* Filters */}
              <div className="flex flex-col sm:flex-row gap-4 mb-6">
                <Input
                  placeholder="Search for a service..."
                  className="h-12"
                  value={search}
                  onChange={(e) => setSearch(e.target.value)}
                />

                <Select
                  value={category}
                  onValueChange={(v) =>
                    setCategory(v as (typeof CATEGORIES)[number])
                  }
                >
                  <SelectTrigger className="h-12 sm:w-48">
                    <SelectValue />
                  </SelectTrigger>
                  <SelectContent>
                    {CATEGORIES.map((c) => (
                      <SelectItem key={c} value={c}>
                        {c}
                      </SelectItem>
                    ))}
                  </SelectContent>
                </Select>
              </div>

              {/* Featured Carousel (WITH wheel gestures) */}
              <SmartCarousel wheel className="mb-6">
                {featured
                  .filter((f) => category === "All" || f.cat === category)
                  .map((f) => (
                    <CarouselItem key={f.id} className="basis-auto pl-3">
                      <FeaturedCard
                        item={f}
                        onEdit={() => openEditModal(f)}
                        onDelete={() => deleteService(f)}
                      />
                    </CarouselItem>
                  ))}
              </SmartCarousel>

              {/* ================= TABLE ================= */}
              <div className="overflow-x-auto">
                <table className="w-full">
                  <thead className="border-b">
                    <tr>
                      <th className="p-3">Service</th>
                      <th className="p-3 hidden sm:table-cell">Category</th>
                      <th className="p-3 hidden md:table-cell">Duration</th>
                      <th className="p-3 hidden md:table-cell">Image</th>
                      <th className="p-3">Price</th>
                      <th className="p-3 text-right">Actions</th>
                    </tr>
                  </thead>

                  <tbody className="divide-y">
                    {filteredRows.map((r) => (
                      <tr key={r.id} className="hover:bg-primary/5">
                        <td className="p-3">{r.title}</td>
                        <td className="p-3 hidden sm:table-cell">{r.cat}</td>
                        <td className="p-3 hidden md:table-cell">{r.dur}</td>
                        <td className="p-3 hidden md:table-cell">
                          {r.image ? (
                            <div
                              className="w-16 h-10 bg-cover bg-center rounded border"
                              style={{ backgroundImage: `url(${r.image})` }}
                            />
                          ) : (
                            "—"
                          )}
                        </td>
                        <td className="p-3">{r.price}</td>
                        <td className="p-3 text-right">
                          <button
                            className="p-2 rounded hover:bg-blue-100 dark:hover:bg-blue-900"
                            onClick={() => openEditModal(r)}
                          >
                            ✏️
                          </button>
                          <button
                            className="p-2 rounded hover:bg-red-100 dark:hover:bg-red-900"
                            onClick={() => deleteService(r)}
                          >
                            🗑️
                          </button>
                        </td>
                      </tr>
                    ))}

                    {filteredRows.length === 0 && (
                      <tr>
                        <td colSpan={6} className="p-6 text-center opacity-60">
                          No services match your filters.
                        </td>
                      </tr>
                    )}
                  </tbody>
                </table>
              </div>
            </div>
          </section>
        </div>
      </main>

      {/* ================= MODAL ================= */}
      <Dialog
        open={open}
        onOpenChange={(v) => (!v ? setEditing(null) : null) || setOpen(v)}
      >
        <DialogContent className="sm:max-w-[560px]">
          <DialogHeader>
            <DialogTitle>Edit service</DialogTitle>
            <DialogDescription>Update service details.</DialogDescription>
          </DialogHeader>

          <Form {...editForm}>
            <form
              className="space-y-4"
              onSubmit={editForm.handleSubmit(onEditSubmit)}
            >
              {/* Title */}
              <FormField
                control={editForm.control}
                name="title"
                render={({ field }) => (
                  <FormItem>
                    <FormLabel>Service name</FormLabel>
                    <FormControl>
                      <Input {...field} />
                    </FormControl>
                    <FormMessage />
                  </FormItem>
                )}
              />

              {/* Category & Duration */}
              <div className="grid grid-cols-2 gap-4">
                <FormField
                  control={editForm.control}
                  name="category"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel>Category</FormLabel>
                      <Select
                        value={field.value}
                        onValueChange={field.onChange}
                      >
                        <FormControl>
                          <SelectTrigger>
                            <SelectValue />
                          </SelectTrigger>
                        </FormControl>
                        <SelectContent>
                          {CATEGORIES.filter((c) => c !== "All").map((c) => (
                            <SelectItem key={c} value={c}>
                              {c}
                            </SelectItem>
                          ))}
                        </SelectContent>
                      </Select>
                      <FormMessage />
                    </FormItem>
                  )}
                />

                <FormField
                  control={editForm.control}
                  name="durationMin"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel>Duration (minutes)</FormLabel>
                      <FormControl>
                        <Input type="number" {...field} />
                      </FormControl>
                      <FormMessage />
                    </FormItem>
                  )}
                />
              </div>

              {/* Price & Image */}
              <div className="grid grid-cols-2 gap-4">
                <FormField
                  control={editForm.control}
                  name="price"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel>Price (USD)</FormLabel>
                      <FormControl>
                        <Input type="number" {...field} />
                      </FormControl>
                      <FormMessage />
                    </FormItem>
                  )}
                />

                <FormField
                  control={editForm.control}
                  name="image"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel>Image URL</FormLabel>
                      <FormControl>
                        <Input {...field} />
                      </FormControl>
                      <FormMessage />
                    </FormItem>
                  )}
                />
              </div>

              {/* Preview */}
              {editForm.watch("image") && (
                <div className="rounded overflow-hidden border">
                  <div
                    className="aspect-video bg-cover bg-center"
                    style={{
                      backgroundImage: `url(${editForm.watch("image")})`,
                    }}
                  />
                </div>
              )}

              <DialogFooter>
                <Button
                  type="button"
                  variant="secondary"
                  onClick={() => setOpen(false)}
                >
                  Cancel
                </Button>
                <Button type="submit">Save changes</Button>
              </DialogFooter>
            </form>
          </Form>
        </DialogContent>
      </Dialog>
    </div>
  );
}
